@import "../variables.scss";


form {
    flex : 0 0 60%;
    padding : 2rem;
    box-sizing: border-box;

    .title-form {
        font-size : 2rem;
        color : $base-color-pink;
        font-weight: 600;
        letter-spacing: 6px;
        text-align : center;
    }


    .input-group {
        position: relative;
        height : 45px;
        margin : 3.3rem 0rem;

        label {
            position: absolute;
            left : 2.5rem;
            bottom : 1rem;
            pointer-events: none;
            color : white;
            transition: .3s;
        }

        .icon-input {
            position: absolute;
            left : .5rem;
            bottom : 1rem;
            color : white;
        }

        #show-password-icon {
            width : 10px;
            position : absolute;
            left : 97%;
            bottom : 1rem;
            color : white;
            cursor : pointer;
        }

        input {
            position: relative;
            width: 100%;
            height: 100%;
            background-color : transparent;
            padding-left : 2.5rem;
            color : white;
            outline : none;
            border : none;
            border-bottom : 2px solid white;
            transform-origin: right;
            font-weight: 500;
            transition: .3s;
            font-size : 1.1rem;
        }

        input[type="text"]:valid,
        input[type="text"]:focus,
        input[type="password"]:focus,
        input[type="password"]:valid {
            border-bottom: 2px solid $base-color-pink;
        }

        input[type="text"]:valid ~ label,
        input[type="password"]:valid ~ label,
        input[type="text"]:focus ~ label,
        input[type="password"]:focus ~ label {
            font-weight: 600;
            transform: translateY(-35px);
            font-size : 1.2rem;
            color : $base-color-pink;
        }

        input[type="text"]:focus ~ .icon-input,
        input[type="password"]:focus ~ .icon-input,
        input[type="text"]:valid ~ .icon-input,
        input[type="password"]:valid ~ .icon-input {
            color : $base-color-pink;
        }

    }

    .forgot-password {
        display: block;
        margin-top : -1rem;
        color : white;
        text-align : right;
        font-size : .9rem;
        text-decoration : none;
    }

    .forgot-password:hover {
        color : $base-color-pink;
    }

    .btn-submit {
        display: block;
        margin : auto;
        margin-top : 2.5rem;
        padding : .5rem 1.5rem;
        width : 10rem;
        cursor : pointer;
        justify-self : center;
        border-radius : 25px;
        border : none;
        color : white;
        background-image: linear-gradient(to right,$base-color-pink,#eb2265);
        font-size : 1.2rem;
        font-weight: 500;
        letter-spacing: 2px;
        outline: none;
        transition: .3s;
        transform : scale(1);
    }

    .btn-submit:hover {
        transform : scale(1.1);
    }
}